.editor-wrapper {
  min-width: 800px;
  border: 1px solid #d1d1d1;
  border-radius: 5px;
  margin-bottom: 10px;
}
.message-list-box {
  min-width: 800px;
}
.message-list-box .avatar-img {
  width: 64px;
  height: 64px;
  object-fit: cover;
}
.message-list-box .avatar-img-small {
  width: 56px;
  height: 56px;
  object-fit: cover;
}
.message-list-box .my-iconfont {
  display: inline-block;
  transform: translateY(1px);
}
.message-list-box .info-box img {
  max-width: 80% !important;
  max-height: 500px !important;
  object-fit: contain;
}
.message-list-box .info-box video {
  max-width: 80% !important;
}
.message-list-box .toggle-reply-box {
  color: #79a5e5;
  cursor: pointer;
  margin-bottom: 20px;
}
.score-box {
  position: fixed;
  right: 50px;
  top: 220px;
  width: 250px;
}
.score-box .info {
  position: relative;
  display: flex;
  align-items: center;
}
.score-box .info .average-num {
  font-size: 36px;
  color: #494949;
  margin-right: 10px;
  font-family: Helvetica;
}
.score-box .info .people-num {
  font-size: 12px;
  color: #37a;
}
.score-box .info .close-box {
  position: absolute;
  top: 0;
  right: 10px;
  font-size: 12px;
  color: #999;
}
.score-box .star-item {
  display: flex;
  justify-items: center;
  margin-top: 5px;
}
.score-box .star-item .star-label {
  flex-shrink: 0;
  font-size: 13px;
  margin-right: 10px;
  color: rgba(0, 0, 0, 0.45);
  transform: translateY(3px);
}
